.cabecalho-principal {
  background-color: var(--cabecalho-e-footer);
  width: 100vw;
  box-sizing: border-box;
  box-shadow: 5px 0px 10px 10px var(--shadow);
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px 3vw;
  display: flex;
}

.cabecalho-principal__titulo {
  color: var(--branco);
  margin-left: 10px;
  padding: 10px;
  font-size: 2.5rem;
  display: inline-block;
}

.cabecalho-principal__titulo-content {
  display: block;
}

.cabecalho-principal__titulo-content:before {
  content: "";
  width: 50px;
  min-height: 30px;
  box-sizing: border-box;
  background-image: url("Item_Ramen_Tonkotsu.c7d855a8.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-right: 8px;
  display: inline-block;
}

.cabecalho-principal__nav {
  flex-grow: 2;
  flex-shrink: 0;
  align-items: center;
  display: flex;
}

.cabecalho-principal__nav-lista {
  width: 100%;
  flex-shrink: 0;
  justify-content: flex-end;
  display: flex;
}

.cabecalho-principal__nav-lista-item {
  flex-shrink: 0;
  padding: 5px 10px;
  display: inline;
}

.cabecalho-principal__nav-lista-link {
  color: var(--branco);
  background-color: var(--botao-base);
  border: 2px solid var(--branco);
  border-radius: 5px;
  flex-shrink: 0;
  padding: 10px;
  font-size: 1.25rem;
  font-weight: bold;
  text-decoration: none;
  transition: all .5s;
  display: block;
}

.cabecalho-principal__nav-lista-link:hover {
  background-color: var(--botao-hover);
}

.cabecalho-principal__nav-lista-link.ativo {
  background-color: var(--botao-ativo);
}

@media screen and (max-width: 1024px) {
  .cabecalho-principal, .cabecalho-principal__nav-lista {
    justify-content: center;
  }
}

@media screen and (max-width: 768px) {
  .cabecalho-principal__nav-lista-link {
    font-size: 1rem;
    display: inline-block;
  }

  .cabecalho-principal__nav-lista {
    max-width: 100vw;
    flex-wrap: wrap;
  }
}

